<template>
  <div class="login-message" id="login-message" v-show="isShowRegistrySelect">
    <div class="hw-overlay" id="hw-layer">
				<div class="hw-layer-wrap">
					<span class="glyphicon glyphicon-remove hwLayer-close" @click="hideLayer">
            <img src="../assets/cancle.png" alt="">
          </span>
					<div class="row">
						<div class="col-md-3 col-sm-12 hw-icon">
							<i class="glyphicon glyphicon-info-sign"></i>
						</div>
						<div class="col-md-9 col-sm-12">
							<el-tabs v-model="activeName" @tab-click="handleClick">
              <el-tab-pane label="注册" name="first">
                <el-form ref="form" :model="form" label-width="60px">
                  <el-form-item label="用户名">
                    <el-input v-model="form.username"></el-input>
                  </el-form-item>
                  <el-form-item label="密码">
                    <el-input type="password" v-model="form.password"></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                  </el-form-item>
                </el-form>
              </el-tab-pane>
            </el-tabs>
            <el-button class="btn-warning" @click="hideLayer" type="text">取消</el-button>
						</div>
					</div>
				</div>
			</div>
  </div>
</template>
<script>
import axios from 'axios' 
export default {
  name: 'LoginSelect',
  data() {
    return {
      activeName: 'first',
      isShowRegistrySelect: 1,
      userId: 0,
      loginData: {},
      form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    hideLayer() {
      this.isShowRegistrySelect = !this.isShowRegistrySelect
    },
    wxRegistry() {
      axios.post('/yunji/account/registry', {
        username: this.form.username,
        password: this.form.password
      })
      .then(this.getRegistryInfo)
      .catch(function (error) {
        console.log(error);
      });
    },
    getRegistryInfo(res) {
      if (res.status == 200) {
        localStorage.loginState = 1
        this.loginData = res.data.retBean
        this.userId = res.data.retBean.userId
        localStorage.userId = this.userId
        localStorage.userName = this.nickName
        localStorage.loginName =
          this.loginData.nickName.length > 6
            ? this.loginData.nickName.substring(0, 6)
            : this.loginData.nickName
        // this.getUserTag(this.userId)
      }
    },
    getUserTag(userId) {
      axios
        .get('/yunji/auth/userTag?userId=' + userId)
        .then(this.getUserTagInfo)
    },
    getUserTagInfo(res) {
      localStorage.tag = JSON.stringify(res.data.retBean[0])
      this.$router.push('/')
      window.location.reload()
    },
    handleClick(tab, event) {
      console.log(tab, event)
    },
    onSubmit() {
      this.wxRegistry()
      console.log('注册成功')
    }
  },
  mounted() {
    // this.showLayer('')
  }
}
</script>
<style scoped>
@import '../common/css/reset.css';
.hw-overlay {
  /* display: none; */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 10;
}
.el-input {
  width: 60%;
}
.hw-layer-wrap {
  box-sizing: border-box;
  width: 450px;
  position: absolute;
  left: 50%;
  top: 15%;
  margin-left: -285px;
  border-radius: 3px;
  background-color: #fff;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.12);
  padding: 45px 50px;
}
.hwLayer-close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  font-size: 20px;
  color: #ccc;
}
.hwLayer-close > img {
  width: 20px;
  height: 20px;
}
.line8 {
  width: 100%;
  height: 1px;
  background-color: grey;
  margin-top: 5px;
}
.btn-warning {
  float: right;
}
.hw-layer-wrap .hw-icon {
  color: #b4d8f3;
  font-size: 86px;
  text-align: center;
}
.hw-layer-wrap h4 {
  margin: 5px 0 30px;
  font-size: 24px;
  color: #383c3e;
}
.hw-layer-wrap p {
  margin: 30px 0;
  line-height: 22px;
  color: #595d60;
  text-align: left;
}
.hw-layer-title {
  height: 32px;
  line-height: 32px;
  font-size: 16px;
  position: absolute;
  left: 20px;
  top: 10px;
  font-weight: bold;
}

@media (max-width: 768px) {
  .hw-layer-wrap {
    width: 350px;
    margin-left: -175px;
    margin-top: -200px;
    padding: 45px 50px;
    text-align: center;
  }
}
@media (max-width: 400px) {
  .hw-layer-wrap {
    width: 250px;
    margin-left: -125px;
    padding: 25px 30px;
  }
}
</style>



