<template>
  <div class="detail">
    <div>
      <h1>{{res.title}}</h1>
      <div style="margin: 20px 0;">
        <ul class="action-bottom">
          <li class>
            <img :src="res.headImageUrl" class="headImage" alt>
          </li>
          <li style>
            <div class="auther">
              <span>&nbsp;&nbsp;作者：{{ res.username }}</span>
              <span>&nbsp;&nbsp;时间：{{ res.createtime | filerTime }}</span>
            </div>
          </li>
          <li style="margin-left: 30px;">
            <el-tooltip placement="top">
              <div slot="content">
                <p style="text-align: center;">分享到朋友圈</p>
                <p>
                  <img
                    id="qr"
                    title="本文二维码，手机扫一扫，精彩随身带!"
                    style="cursor:pointer;"
                    :src="shageImageUrl"
                    alt="扫码分享到朋友圈"
                  >
                </p>
              </div>
              <span>
                &nbsp;
                <i class="icon iconfont icon-friends" style="color: green"></i>
                分享到朋友圈
              </span>
            </el-tooltip>
          </li>
          <li>
            <el-tooltip placement="top">
              <div slot="content">
                <p style="text-align: center;">分享到小程序</p>
                <p>
                  <img
                    id="qr"
                    title="本文二维码，手机扫一扫，精彩随身带!"
                    style="cursor:pointer; width: 200px; height: 200px;"
                    src="../assets/xiaochengxu.jpg"
                    alt="扫码在小程序中查看"
                  >
                </p>
              </div>
              <span>
                &nbsp;
                <i class="icon iconfont icon-xiaochengxu" style="color: green"></i>
                分享到小程序
              </span>
            </el-tooltip>
          </li>
        </ul>
        <div style="clear:both;"></div>
      </div>
    </div>

    <div id="htmlUrl" v-html="res.htmlUrl"></div>

    <div>
      <ul class="action-bottom" style="margin: 20px 0;">
        <li class>
          <span>
            <i class="icon iconfont icon-pinglun"></i>
          </span>
          <span>
            {{res.commentNum ? res.commentNum + '条评论' : '0条评论'}}
            &nbsp;
          </span>
        </li>
        <li>
          <i class="icon iconfont icon-like"></i>
          {{res.starNum ? res.starNum + '点赞' : '0点赞'}}
          &nbsp;
        </li>
        <li>
          <i class="icon iconfont icon-eye"></i>
          <span style="margin-bottom: 10px;">{{res.readNum ? res.readNum + '阅读' : '0阅读'}}</span>
          &nbsp;
        </li>
        <li>
          <el-tooltip placement="top">
            <div slot="content">
              <p style="text-align: center;">分享到朋友圈</p>
              <p>
                <img
                  id="qr"
                  title="本文二维码，手机扫一扫，精彩随身带!"
                  style="cursor:pointer;"
                  :src="shageImageUrl"
                  alt="扫码分享到朋友圈"
                >
              </p>
            </div>
            <span>
              &nbsp;
              <i class="icon iconfont icon-friends" style="color: green"></i>
              分享到朋友圈
            </span>
          </el-tooltip>
        </li>
        <li>
          <el-tooltip placement="top">
            <div slot="content">
              <p style="text-align: center;">分享到小程序</p>
              <p>
                <img
                  id="qr"
                  title="本文二维码，手机扫一扫，精彩随身带!"
                  style="cursor:pointer; width: 200px; height: 200px;"
                  src="../assets/xiaochengxu.jpg"
                  alt="扫码在小程序中查看"
                >
              </p>
            </div>
            <span>
              &nbsp;
              <i class="icon iconfont icon-xiaochengxu" style="color: green"></i>
              分享到小程序
            </span>
          </el-tooltip>
        </li>
      </ul>
      <div style="clear:both;"></div>
      <div class="line1" v-show="comments.length ? 1 : 0" style="margin: 10px 0;"></div>
      <div v-for="item in comments">
        <div style="margin: 10px 0;">
          <img :src="item.headImageUrl ? item.headImageUrl : ''" class="commentImage" alt>
          &nbsp;&nbsp;{{ item.nickName }}
          &nbsp;&nbsp;{{ item.createtime | filerTime }}
        </div>
        <div>&nbsp;{{item.content}}</div>
      </div>
    </div>
  </div>
</template>
<script scoped>
import axios from "axios";
export default {
  name: "Detail",
  data() {
    return {
      res: {},
      shageImageUrl: "",
      imgClass: "img",
      comments: [
        {
          // articleId: 1000000989,
          // content: "这是评论内容",
          // createtime: "2018-11-16 09:09:29",
          // headImageUrl: "https://wx.qlogo.cn/mmopen/vi_32/Q0j4TwGTfTKy5cN02icQ0bcCia976djfJbNSd5YVpjoIiaib6NAqAeO8NoJJVpH6PR5mTEflaDa3cHXlibjoEwR1u4A/132",
          // nickName:"Lin",
          // userId: 10000182
        }
      ]
    };
  },
  methods: {
    getArticleId() {
      var hrefUrl = window.location.href;
      var articleIdArr = hrefUrl.indexOf(".html")
        ? hrefUrl.replace(".html", "")
        : hrefUrl;
      var articleId = articleIdArr.slice(-10);
      console.log(articleId);
      return articleId;
    },
    getArticle() {
      var articleId = this.getArticleId();
      console.log(articleId);
      axios
        .get("/yunji/article/detail?articleId=" + articleId)
        .then(this.getArticleInfo);
    },
    getArticleInfo(res) {
      if (res.status == 200) {
        this.res = res.data.retBean;
      }
    },
    getComment() {
      var articleId = this.getArticleId();
      axios
        .get("/yunji/article/comment?articleId=" + articleId)
        .then(this.getCommentInfo);
    },
    getCommentInfo(res) {
      if (res.status == 200) {
        this.res = res.data.retBean;
        this.comments = res.data.retBean;
        console.log(res);
      }
    },
    getShareImage() {
      this.shageImageUrl =
        "http://qr.liantu.com/api.php?&w=200&text=" + window.location.href;
    },
    getQRcode() {
      // var id = document.getElementById('qr').innerHTML
    },
    changeClass() {
      var imgList = document.getElementsByTagName("img");
      for (var i = 0; i < imgList.length; i++) {
        imgList[i].style.width = "100%";
      }
    }
  },
  filters: {
    filerTime: function(value) {
      if (!value) return "";
      value = value.toString();
      return value.substring(0, 10);
    }
  },
  mounted() {
    this.getArticle();
    this.getComment();
    this.getShareImage();
    // this.getQRcode()
  },
  updated() {
    this.changeClass();
  }
};
</script>
<style scoped>
/* @import '../common/css/reset.css'; */
@media (min-width: 992px) {
  .detail {
    width: calc(60% - 40px);
    margin-left: 20%;
    background: #ffffff;
    padding-top: 30px;
    padding-left: 20px;
    padding-right: 20px;
    /* min-width: 750px; */
    min-height: 750px;
  }
}
@media (max-width: 992px) {
  .detail {
    width: 100%;
    background: #ffffff;
    padding-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 30px;
  }
}
.auther > span {
  display: inherit;
  line-height: 16px !important;
}
.icon-span {
  display: inline-flex;
  align-items: center;
}
code {
  width: 80%;
  color: red;
}
p pre code {
  word-wrap: break-word;
  word-break: break-all;
}
pre code {
  white-space: pre-wrap; /* css-3 */
  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
  white-space: -pre-wrap; /* Opera 4-6 */
  white-space: -o-pre-wrap; /* Opera 7 */
  word-wrap: break-word; /* Internet Explorer 5.5+ */
}
.action-bottom li {
  float: left;
  height: 32px;
  line-height: 32px;
  display: block;
}
.line1 {
  width: calc(100%);
  height: 1px;
  background: rgb(165, 162, 162);
}
.icon {
  /* font-size: 22px; */
  margin-top: 10px;
  height: 32px;
  line-height: 32px;
}
.headImage {
  width: 30px !important;
}
.el-tooltip__popper.is-dark {
  background-color: grey !important;
  color: red !important;
}
.commentImage {
  width: 30px !important;
  height: 30px;
  border-radius: 15px;
}
</style>



