<template>
  <div style="width: 100%; background: #ffffff;">
    <div :class='{popContainer: isShowWrite}'></div>
    <div :class='{popContainer: isShowLoginSelect}'></div>
    <div class="header">
      <div class="header-img">
      </div>
      <div class="header-title">
        <router-link to='/' style="font-size: 22px;" class="header-write">
          云笈<i class="icon iconfont icon-dian" style="color: grey;"></i>懂秘&nbsp; <span style="font-size: 18px;">精选的视界</span>
        </router-link>
      </div>
      <div class="header-a">
        <a class="write" @click="showWrite" style="margin-right: 5px; color: #3399EA;" > &nbsp;&nbsp;发布</a>
        <a class="" @click="showLoginSelect" style="margin-right: 5px; color: #3399EA;" v-show="!loginState">登录</a>
        <el-dropdown trigger="click" style="display: none;" v-show="loginState" @command="handleCommand">
          <span class="el-dropdown-link" style="color: #0f0f0f; font-size: 18px;">
            <img :src="headimageurl" class="headimageurl" alt="">
            <span style="font-size: 14px; color: #3399EA;">{{ loginName }}</span>
            <i class="el-icon-arrow-down el-icon--right" style="color: #3399EA;">
            </i>
          </span>
          <el-dropdown-menu slot="dropdown" @click="loginOut()">
            <el-dropdown-item command="a">&nbsp;&nbsp;退出&nbsp;&nbsp;</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <login-select @getLoginData="receiveLogin" class="showSelect" v-if="isShowLoginSelect"></login-select>
    <show-write @getdata="receive" v-if="isShowWrite"></show-write>
    <registry-select class="showSelect" v-if="isSHowRegistrySelect"></registry-select>
  </div>
</template>

<script scoped>
import axios from 'axios'
import LoginSelect from '@/components/LoginSelect'
import RegistrySelect from '@/components/RegistrySelect'
import ShowWrite from '@/components/ShowWrite'
export default {
  name: 'Header',
  data() {
    return {
      isShowLoginSelect: 0,
      isSHowRegistrySelect: 0,
      isShowWrite: 0
    }
  },
  components: {
    LoginSelect,
    RegistrySelect,
    ShowWrite
  },
  computed: {
    loginState() {
      return parseInt(localStorage.loginState)
    },
    loginName() {
      return localStorage.loginName
    },
    headimageurl() {
      return localStorage.headimageurl
    }
  },
  methods: {
    showWrite(){
      this.isShowWrite = !this.isShowWrite
    },
    receive:function(val) {
        this.isShowWrite = val;
    },
    receiveLogin:function(val) {
        this.isShowLoginSelect = val;
    },
    showLoginSelect() {
      this.isShowLoginSelect = !this.isShowLoginSelect
      // this.wxLoginCode()
    },
    showRegistry() {
      this.isSHowRegistrySelect = !this.isSHowRegistrySelect
    },
    handleCommand(command) {
      this.loginOut()
    },
    getLocalStorageValue() {
      this.loginState = parseInt(localStorage.loginState)
      this.loginName = localStorage.loginName
    },
    loginOut() {
      localStorage.userId = 0
      localStorage.loginState = 0
      this.loginState = 0
      localStorage.loginName = ''
      localStorage.code = ''
      localStorage.headimageurl = ''
      window.location.reload()
      // this.$router.push('/')
    },
    wxLoginCode() {
      var that = this
      var code = localStorage.code
      if (code && localStorage.loginState != 1) {
        axios.get('/yunji/auth/wxLogin?code=' + code).then(this.getwxLoginInfo)
      }
    },
    getwxLoginInfo(res) {
      if (res.status == 200) {
        if (res.data.retBean) {
          console.log(res.data.retBean)
          localStorage.loginState = 1
          this.loginData = res.data.retBean
          this.userId = res.data.retBean.userId
          localStorage.headimageurl = res.data.retBean.headimageurl
          localStorage.userId = this.userId
          localStorage.userName = this.loginData.nickname
          localStorage.loginName = this.loginData.nickname
          localStorage.headimageurl = this.loginData.headimageurl
          window.location.href = 'https://www.yunjiworks.com'
          // this.getUserTag(this.userId)
        }
      }
    }
  },
  mounted() {
    this.wxLoginCode()
    // this.getLocalStorageValue()
  }
}
</script>
<style lang="stylus" scoped>
@import '../common/css/iconfont.css';
// @import '../common/css/reset.css';
@import '~styles/varibles.styl';
.popContainer{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,0.3);
}
.headimageurl{
    width: 22px !important;
    height: 22px !important;
    border-radius: 11px;
  }
@media (min-width: 992px) {
  .header {
    width: 60%;
    margin-left: 20%;
    // min-width: 750px;
    background: #ffffff;
    height: $headerHight;
    border-width: 60%;
    border-bottom: solid #f0f0f0;
  }

  .showSelect {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
  }

  .header-img, .header-title {
    float: left;
  }
  .write::hover{
    // color: red;
  }
  .header-logo {
    width: 80px;
    height: $headerHight;
  }
  .header-a {
   
    float: right;
    height: $headerHight;
    line-height: $headerHight;
    font-size: 16px;
  }

  .header-write {
    height: $headerHight;
    line-height: $headerHight;
  }

  .header-write-login {
    color: #0f0f0f !important;
  }

  .el-dropdown-link {
    height: $headerHight;
    line-height: $headerHight;
  }

  .el-popper[x-placement^=bottom] {
    margin-top: 0px !important;
  }

  .header-title {
    height: $headerHight;
    line-height: $headerHight;
  }
}

@media (max-width: 992px) {
  .header {
    width: 100%;
    height: $headerHight;
    border-width: 60%;
    border-bottom: solid #f0f0f0;
  }

  .header-img, .header-title {
    margin-left: 5px;
    float: left;
    height: $headerHight;
    line-height: $headerHight;
  }

  .el-popper[x-placement^=bottom] {
    margin-top: 0px !important;
  }

  .header-a {
    margin-right: 5px;
    float: right;
    // display: none;
    height: $headerHight;
    line-height: $headerHight;
    font-size: 16px;
  }
}
@media (max-width: 750px) {
  .header-a {
    margin-right: 5px;
    float: right;
    display: none;
    height: $headerHight;
    line-height: $headerHight;
    font-size: 16px;
  }
}
</style>



