<template>
  <div class="login-message" id="login-message" v-show="isShowLoginSelect">
    <div class="hw-overlay" id="hw-layer">
				<div class="hw-layer-wrap">
					<span class="glyphicon glyphicon-remove hwLayer-close" @click="hideLayer">
            <img src="../assets/cancle.png" alt="">
          </span>
					<div class="row">
						<div class="col-md-3 col-sm-12 hw-icon">
							<i class="glyphicon glyphicon-info-sign"></i>
						</div>
						<div class="col-md-9 col-sm-12">
							<!-- <h5>请选择登录方式</h5>
              <div class="line8"></div> -->
							<el-tabs v-model="activeName" @tab-click="handleClick">
              <el-tab-pane label="微信" name="first">
                <span id="login_container"></span>
                <br>
                <span>微信</span>
              </el-tab-pane>
              <el-tab-pane label="github" name="second">
                <img src="../assets/weixin.png" alt="">
                <br>
                <span>github</span>
              </el-tab-pane>
              <el-tab-pane label="登录" name="third">
                <el-form ref="form" :model="form" label-width="60px">
                  <el-form-item label="用户名">
                    <el-input v-model="form.username"></el-input>
                  </el-form-item>
                  <el-form-item label="密码">
                    <el-input type="password" v-model="form.password"></el-input>
                  </el-form-item>
                  <el-form-item label="">
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                  </el-form-item>
                </el-form>  
              </el-tab-pane>
            </el-tabs>
            <el-button class="btn-warning" @click="hideLayer" type="text">取消</el-button>
						</div>
					</div>
				</div>
			</div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'LoginSelect',
  data() {
    return {
      activeName: 'first',
      isShowLoginSelect: 1,
      userId: 0,
      loginData: {},
      form: {
        username: '',
        password: ''
      },
      search: ''
    }
  },
  methods: {
    hideLayer() {
      this.isShowLoginSelect = !this.isShowLoginSelect
      this.setValue()
    },
    setValue: function() {
      this.$emit('getLoginData', this.isShowLoginSelect) //this.value为向父组件传递的数据
    },
    wxLogin() {
      var redirect_uri = encodeURIComponent('https://www.yunjiworks.com')
      var obj = new WxLogin({
        self_redirect: false,
        id: 'login_container',
        appid: 'wxd93c945778a1af28',
        scope: 'snsapi_login',
        redirect_uri: redirect_uri,
        state: 'STATE',
        style: '',
        href: ''
      })
    },

    getUserTag(userId) {
      axios
        .get('/yunji/auth/userTag?userId=' + userId)
        .then(this.getUserTagInfo)
    },
    getUserTagInfo(res) {
      // localStorage.tag = JSON.stringify(res.data.retBean[0])
      console.log('登录完成')
      this.$router.push('/')
      window.location.reload()
    },
    handleClick(tab, event) {
      console.log(tab, event)
    },
    onSubmit() {
      axios
        .post('/yunji/auth/login', {
          username: this.form.username,
          password: this.form.password
        })
        .then(this.getwxLoginInfo)
      console.log('登录成功')
    }
  },
  mounted() {
    this.search = location.search
    this.wxLogin()
  }
}
</script>
<style scoped>
@import '../common/css/reset.css';
.el-input {
  width: 60%;
}
.hw-layer-wrap {
  box-sizing: border-box;
  width: 450px;
  position: absolute;
  left: 50%;
  top: 15%;
  margin-left: -285px;
  border-radius: 3px;
  background-color: #fff;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.12);
  padding: 45px 50px;
}
.hwLayer-close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  font-size: 20px;
  color: #ccc;
}
.hwLayer-close > img {
  width: 20px;
  height: 20px;
}
.line8 {
  width: 100%;
  height: 1px;
  background-color: grey;
  margin-top: 5px;
}
.btn-warning {
  float: right;
}
.hw-layer-wrap .hw-icon {
  color: #b4d8f3;
  font-size: 86px;
  text-align: center;
}
.hw-layer-wrap h4 {
  margin: 5px 0 30px;
  font-size: 24px;
  color: #383c3e;
}
.hw-layer-wrap p {
  margin: 30px 0;
  line-height: 22px;
  color: #595d60;
  text-align: left;
}
.hw-layer-title {
  height: 32px;
  line-height: 32px;
  font-size: 16px;
  position: absolute;
  left: 20px;
  top: 10px;
  font-weight: bold;
}

@media (max-width: 768px) {
  .hw-layer-wrap {
    width: 350px;
    margin-left: -175px;
    margin-top: -200px;
    padding: 45px 50px;
    text-align: center;
  }
  #login_container{
    width: 50px;
  }
}
@media (max-width: 400px) {
  .hw-layer-wrap {
    width: 250px;
    margin-left: -125px;
    padding: 25px 30px;
  }
}
</style>



