<template>
  <div class="content">
    <div>
      <div class="content-header">
        <div class="hot-recommend">关注</div>
        <div class="more-recommend">
          <router-link to='/MoreAction'>
            更多关注>>
            <!-- <img src="../assets/more.png" style="height: 22px; line-height: 22px;" alt=""> -->
          </router-link>
        </div>
        <div style="clear: both;"></div>
      </div>
      <div class="line"></div>
      <div class="content-article" v-if="!!loginState">
        <ul class="content-article-ul">
          <li v-for="(list, index) in reportList" class="content-article-li">
          <router-link :to="{name: 'detail', params: { id: (list.userId).toString() + list.articleId }}">
            <div class="article-headline"><h1>{{ list.title }}</h1></div>
            <div class="article-content">
              <p>
                {{ list.summary }}
              </p>
            </div>
            <div class="article-information">
              <div class="information-img"><img :src="list.headImageUrl" alt="" class="information-img-profile"></div>
              <div class="information-name">{{list.username}}</div>
              <div class="information-date">{{ list.createtime }}</div>
              <div style="clear: both;"></div>
            </div>
            <div class="line"></div>
          </router-link>
        </li>
        </ul>
      </div>
    </div>
    <div>
      <div class="content-header">
      <div class="hot-recommend">热点推荐</div>
      <div class="more-recommend">
          <router-link to='/moreRecommend'>
            更多推荐>>
            <!-- <img src="../assets/more.png" style="height: 22px; line-height: 22px;"  alt=""> -->
          </router-link>
        </div>
        <div style="clear: both;"></div>
      </div>
      <div class="line"></div>
      <div class="content-article">
        <ul class="content-article-ul">
          <li v-for="(list, index) in reversedArticleList" class="content-article-li">
          <router-link :to="{name: 'detail', params: { id: (list.userId).toString() + list.articleId }}">
            <div class="article-headline"><h1>{{ list.title }}</h1></div>
            <div class="article-content">
              <p>
                {{ list.summary }}
              </p>
            </div>
            <div class="article-information">
              <div class="information-img"><img :src="list.headImageUrl" alt="" class="information-img-profile"></div>
              <div class="information-name">{{ list.username }}</div>
              <div class="information-date">{{ list.createtime | filerTime }}</div>
              <div style="clear: both;"></div>
            </div>
            <div class="line"></div>
          </router-link>
        </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import MoreAction from '@/components/MoreAction'
import axios from 'axios'
export default {
  name: 'Header',
  data() {
    return {
      articleList: [],
      userIdarticleId: '',
      articleId: '',
      loginState: 0,
      params: {
        userId: 0,
        pagesize: '3',
        tagList: []
      },
      reportList: []
    }
  },
  components: {
    MoreAction
  },
  methods: {
    getLocalStorageValue() {
      this.loginState = localStorage.loginState || 0
      this.params.userId = localStorage.userId
      if (localStorage.userId) {
        this.getReportList()
      }
    },
    getArticleList() {
      const _this = this
      axios
        .get('/yunji/article/webRecArticles?' + 'pagesize=' + 3)
        .then(this.getArticleListInfo)
    },
    getArticleListInfo(res) {
      if (res.data.retBean.length) {
        const _this = this
        this.articleList = this.articleList.concat(res.data.retBean)
        if (res.data.retBean.length == 0) {
        }
      }
    },
    getMoreArticleList() {
      this.getArticleList()
    },
    getReportList() {
      axios({
        method: 'post',
        url: '/yunji/article/report',
        headers: {
          'Content-Type': 'application/json'
        },
        data: JSON.stringify(this.params)
      }).then(this.getReportListInfo)
    },
    getReportListInfo(res) {
      if (res.data.retVal == 0) {
        const _this = this
        this.pagenum++
        this.reportList = this.reportList.concat(res.data.retBean)
        if (res.data.retBean.length == 0) {
          console.log('没有更多了！')
        }
      }
    },
    getMoreReportList() {
      this.getReportList()
    }
  },
  filters: {
    capitalizeTime: function(shijianchuo) {
      var time = new Date(shijianchuo)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      return y + '-' + m + '-' + d
    },
    filterRecommend: function(array){
      
    },
    filerTime: function(value){
      if (!value) return ''
      value = value.toString()
      return value.substring(0,10)
    }
  },
  computed: {
    reversedArticleList: function(){
      var arr = []
      for (var i = 0; i< this.articleList.length; i++){
        if(this.articleList[i].htmlUrl){
          arr.push(this.articleList[i])
        }
      }
      return arr
    }
  },
  mounted() {
    this.getLocalStorageValue()
    this.getArticleList()
    // this.getReportList()
  }
}
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl';
@import '../common/css/reset.css';

@media (min-width: 992px) {
  .content {
    width: 60%;
    margin-left: 20%;
    // min-width: 750px;
  }
}

@media (max-width: 992px) {
  .content {
    width: 100%;
  }
}

.content {
  background: #ffffff;

  .content-header {
    height: 40px;
    line-height: 40px;

    .hot-recommend {
      padding-left: 20px;
      float: left;
    }

    .more-recommend {
      padding-right: 20px;
      float: right;
    }
  }

  .line {
    // width: calc(100% + 40px);
    // padding-left: -40px;
    height: 2px;
    background: #f5f6f7;
  }

  .content-article {
    width: 100%;

    .content-article-li {
      padding-top: 20px;
      padding-left: 20px;
      padding-right: 20px;

      .article-headline {
        color: #0f0f0f;
        font-size: 22px;
        margin-bottom: 15px;
      }

      .article-content {
        color: #666;
        font-size: 14px;
        margin-bottom: 10px;
      }

      .article-information {
        margin-bottom: 10px;
      }

      .information-img {
        float: left;

        .information-img-profile {
          width: 30px;
          height: 30px;
        }
      }

      .information-name {
        float: left;
        margin-left: 15px;
        color: #666;
        height: 30px;
        line-height: 30px;
      }

      .information-date {
        color: #666;
        margin-left: 15px;
        float: left;
        height: 30px;
        line-height: 30px;
      }
    }

    li:hover {
      background: #f5f6f7;
    }
  }
}
</style>



