<template>
  <div class="write">
    <div class="write-header">
      <div class="uploading">上传文章</div>
      <div style="clear: both;"></div>
    </div>
    <div class="line"></div>
    <form>
      <el-row :gutter="20">
        <el-col :span="6" :offset="3">
          <div class="grid-content bg-purple grid-padding">标题</div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple grid-padding">
            <el-input v-model="title"></el-input>
          </div>
        </el-col>
      </el-row>
      <el-row style="padding-top: 20px" :gutter="20">
        <el-col :span="6" :offset="3">
          <div class="grid-content bg-purple grid-padding">文件上传</div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple grid-padding">
            <a href="javascript:;" class="file a-upload">
              选择文件
              <input type="file" name id @change="getFile($event)">
            </a>
            <a style="display: block" class="showFileName">{{fileName}}</a>
            <span style="display: block; margin-top: 10px;">备注：只能上传.md格式的文件</span>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20">
        <el-col :span="12" :offset="10">
          <div class="grid-content bg-purple grid-padding">
            <el-button @click="submitForm($event)" type="primary">提交</el-button>
          </div>
        </el-col>
      </el-row>
    </form>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      checkList: [],
      file: "",
      fileName: "",
      userName: "",
      title: "",
      userId: 0
    };
  },
  mounted() {
    this.getLocalStorageValue();
  },
  methods: {
    getLocalStorageValue() {
      this.userId = localStorage.userId;
      this.userName = localStorage.userName;
      if (parseInt(localStorage.loginState)) {
      } else {
        this.$message({
          message: "请先登录，才可以写文章",
          type: "warning"
        });
        this.$router.push("/");
      }
    },
    getFile(event) {
      this.file = event.target.files[0];
      console.log(this.file);
      this.fileName = this.file.name;
    },
    successMessage() {
      this.$message({
        message: "上传提交成功",
        type: "success"
      });
    },
    submitForm(event) {
      event.preventDefault();
      console.log(localStorage.userName)
      let formData = new FormData();
      formData.append("userId", this.userId);
      // formData.append('list', this.checkList)
      formData.append("username", "localStorage.userName");
      formData.append("title", this.title);
      formData.append("file", this.file);
      formData.append("organization", "云笈科技");
      if (this.file.length == 0) {
        this.$message({
          message: "请选择一个上传的md文件",
          type: "warning"
        });
        return;
      }
      let config = {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      };
      let _this = this;
      axios
        .post("/yunji/file/fileupload", formData, config)
        .then(function(res) {
          _this.successMessage();
          console.log("上传成功");
          setTimeout(function() {
            _this.$router.push("/");
          }, 1000);
        });
    }
  }
};
</script>
<style lang="stylus" scoped>
// @import '../common/css/reset.css';
@import '~styles/varibles.styl';

@media (min-width: 992px) {
  .write {
    width: 60%;
    margin-left: 20%;
    min-width: 750px;
    min-height: 750px;
    background: #ffffff;
  }
}

@media (max-width: 992px) {
  .write {
    width: 100%;
    // margin-left: 20%;
    // min-width: 750px;
    min-height: 750px;
    background: #ffffff;
  }
}

.write-header {
  height: 40px;
  line-height: 40px;
}

.uploading {
  padding-left: 20px;
  float: left;
}

.line {
  width: calc(100% + 40px);
  padding-left: -40px;
  height: 2px;
  background: #f5f6f7;
}

.grid-padding {
  padding: 20px 0px 10px 0;
}

.content-article {
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 30px;
}
</style>



