<template>
  <div class="content">
    <div class="content-header">
      <div class="hot-recommend">关注</div>
      <div class="more-recommend">
        <router-link to='/MoreAction'>
          <!-- 更多关注 -->
        </router-link>
      </div>
      <div style="clear: both;"></div>
    </div>
    <div class="line"></div>
    <div class="content-article">
      <ul class="content-article-ul">
        <li v-for="(list, index) in reportList" v-if="reportList.length" class="content-article-li">
          <router-link :to="{name: 'detail', params: { id: (list.userId).toString() + list.articleId }}">
            <div class="article-headline"><h1>{{ list.title }}</h1></div>
            <div class="article-content">
              <p>
                {{ list.summary | capitalizeSummary }}
              </p>
            </div>
            <div class="article-information">
              <div class="information-img"><img :src="list.headImageUrl" alt="" class="information-img-profile"></div>
              <div class="information-name">{{ list.username }}</div>
              <div class="information-date">{{ list.createtime }}</div>
              <div style="clear: both;"></div>
            </div>
            <div class="line"></div>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="more-recommend-bottom">
      <el-button type="info" class="button-info" @click="getMoreReportList">更多关注</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'MoreAction',
  data() {
    return {
      params: {
        userId: '',
        pagesize: '3',
        articleId: ''
      },
      reportList: []
    }
  },
  methods: {
    getLocalStorageValue() {
      this.params.userId = localStorage.userId
      console.log(localStorage.loginState)
      console.log('ddd')
      if (parseInt(localStorage.loginState)) {
        this.getReportListFirst()
      } else {
        this.$message({
          message: '请先登录，才可以查看',
          type: 'warning'
        })
        this.$router.push('/')
      }
    },
    getReportList() {
      axios({
        method: 'get',
        url:
          '/yunji/article/articlesRecommend?userId=' +
          this.params.userId +
          '&pagesize=' +
          this.params.pagesize +
          '&articleId=' +
          this.params.articleId,
        headers: {
          'Content-Type': 'application/json'
        },
        data: JSON.stringify(this.params)
      }).then(this.getReportListInfo)
    },
    getReportListFirst() {
      axios({
        method: 'get',
        url:
          '/yunji/article/articlesRecommend?userId=' +
          this.params.userId +
          '&pagesize=' +
          this.params.pagesize,
        headers: {
          'Content-Type': 'application/json'
        },
        data: JSON.stringify(this.params)
      }).then(this.getReportListInfo)
    },
    getReportListInfo(res) {
      const _this = this
      this.reportList = this.reportList.concat(res.data.retBean)
      if (res.data.retBean.length == 0) {
        _this.$message({
          message: '没有更多了！！！',
          type: 'success'
        })
      } else {
        this.params.articleId =
          res.data.retBean[res.data.retBean.length - 1].articleId
      }
    },
    getMoreReportList() {
      this.getReportList()
    }
  },
  filters: {
    capitalizeTime: function(shijianchuo) {
      var time = new Date(shijianchuo)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      return y + '-' + m + '-' + d
    },
    capitalizeSummary(summary) {
      return summary.length > 100 ? summary.slice(0, 200) + '...' : summary
    }
  },
  mounted() {
    this.getLocalStorageValue()
    
  }
}
</script>
<style lang="stylus" scoped>
@import '../common/css/reset.css';
@import '~styles/varibles.styl';

@media (min-width: 992px) {
  .content {
    width: 60%;
    margin-left: 20%;
    // min-width: 750px;
    background: #ffffff;
  }
}

@media (max-width: 992px) {
  .content {
    width: 100%;
    // margin-left: 20%;
    // min-width: 750px;
    background: #ffffff;
  }
}

.content-header {
  height: 40px;
  line-height: 40px;
}

.hot-recommend {
  padding-left: 20px;
  float: left;
}

.more-recommend {
  padding-right: 20px;
  float: right;
}

.line {
  width: calc(100% + 40px);
  padding-left: -40px;
  height: 2px;
  background: #f5f6f7;
}

.content-article {
  width: 100%;
}

.content-article-li {
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.article-headline {
  color: #0f0f0f;
  font-size: 22px;
  margin-bottom: 15px;
}

.article-content {
  color: #666;
  font-size: 14px;
  margin-bottom: 10px;
}

.article-information {
  margin-bottom: 10px;
}

.information-img {
  float: left;
}

.information-img-profile {
  width: 30px;
  height: 30px;
}

.information-name {
  float: left;
  color: #666;
  margin-left: 15px;
  height: 30px;
  line-height: 30px;
}

.information-date {
  margin-left: 15px;
  color: #666;
  float: left;
  height: 30px;
  line-height: 30px;
}

li:hover {
  background: #f5f6f7;
}

.more-recommend-bottom {
  text-align: center;
  padding-bottom: 30px;
  padding-top: 20px;
}

.button-info {
  width: 120px;
  height: 45px;
  line-height: 45px;
}
</style>



