<template>
  <div class="login-message" id="login-message" v-show="isShow">
    <div class="hw-overlay" id="hw-layer">
      <div class="hw-layer-wrap">
        <span class="glyphicon glyphicon-remove hwLayer-close" @click="hideLayer">
          <img src="../assets/cancle.png" alt>
        </span>
        <div class="row">
          <!-- <div class="col-md-3 col-sm-12 hw-icon">
							<i class="glyphicon glyphicon-info-sign"></i>
          </div>-->
          <div class="col-md-12 col-sm-12">
            <form>
              <el-row style="padding-top: 20px">
                <el-col :span="6">
                  <div class="gird-label grid-content bg-purple grid-padding">文件上传</div>
                </el-col>
                <el-col :span="18">
                  <div class="grid-content bg-purple grid-padding">
                    <a href="javascript:;" class="file">
                      选择文件
                      <input type="file" @change="getFile($event)">
                    </a>
                    <a
                      style="height: 30px; line-height: 30px;"
                      class="fileName"
                    >&nbsp;&nbsp;&nbsp;{{fileName}}</a>
                    <span style="display: block; margin-top: 10px;">只能上传.md格式的文件</span>
                    <!-- <el-upload
                            class="upload-demo"
                            action=""
                            :on-preview="handlePreview"
                            :on-remove="handleRemove"
                            :before-remove="beforeRemove"
                            multiple
                            :limit="1"
                            :auto-upload="false"
                            :file-list="fileList">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip" class="el-upload__tip">只能上传md文件</div>
                    </el-upload>-->
                    <!-- <a style="display: block" class="showFileName">{{fileName}}</a> -->
                    <!-- <span style="display: block; margin-top: 10px;">备注：只能上传.md格式的文件</span> -->
                  </div>
                </el-col>
              </el-row>
              <el-row style="padding-top: 20px">
                <el-col :span="6">
                  <div class="gird-label grid-content bg-purple grid-padding">标题</div>
                </el-col>
                <el-col :span="18">
                  <div class="grid-content bg-purple grid-padding">
                    <el-input v-model="title"></el-input>
                  </div>
                </el-col>
              </el-row>

              <el-row style="padding-top: 30px">
                <!-- <el-col :span="6">
                        <div class="grid-content bg-purple grid-padding">
                        </div>
                </el-col>-->
                <el-col :span="8" :offset="6">
                  <div class="grid-content bg-purple grid-padding">
                    <el-button @click="submitForm($event)" type="primary">提交</el-button>
                  </div>
                </el-col>
                <el-col :span="6">
                  <div class="grid-content bg-purple grid-padding">
                    <el-button @click="hideLayer" type>取消</el-button>
                  </div>
                </el-col>
              </el-row>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "ShowWrite",
  data() {
    return {
      isShow: 1,
      file: "",
      fileName: "",
      userName: "",
      title: "",
      userId: 0
    };
  },
  methods: {
    hideLayer() {
      this.isShow = !this.isShow;
      this.setValue();
    },
    setValue: function() {
      this.$emit("getdata", this.isShow); //this.value为向父组件传递的数据
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handlePreview(file) {
      console.log(file);
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    getLocalStorageValue() {
      this.userId = localStorage.userId;
      this.userName = localStorage.userName;
      if (parseInt(localStorage.loginState)) {
      } else {
        this.$message({
          message: "请先登录，才可以发布",
          type: "warning"
        });
        this.hideLayer();
        // this.$router.push('/')
      }
    },
    getFile(event) {
      this.file = event.target.files[0];
      console.log(this.file);
      this.fileName = this.file.name;
    },
    successMessage() {
      this.$message({
        message: "上传提交成功",
        type: "success"
      });
      this.hideLayer();
      location.reload();
    },
    submitForm(event) {
      event.preventDefault();
      let formData = new FormData();
      formData.append("userId", this.userId);
      // formData.append('list', this.checkList)
      formData.append("username", this.userName);
      formData.append("title", this.title);
      formData.append("file", this.file);
      formData.append("organization", "云笈科技");
      if (this.file.length == 0) {
        this.$message({
          message: "请选择一个上传的md文件",
          type: "warning"
        });
        return;
      }
      let config = {
        headers: {
          "Content-Type": "multipart/form-data"
        }
      };
      let _this = this;
      axios
        .post("/yunji/file/fileupload", formData, config)
        .then(function(res) {
          _this.successMessage();
          console.log("上传成功");
          setTimeout(function() {
            _this.$router.push("/");
          }, 1000);
        });
    }
  },
  mounted() {
    this.getLocalStorageValue();
  }
};
</script>
<style scoped>
/* @import '../common/css/reset.css'; */
.login-message {
  background-color: rgba(0, 0, 0, 0.8);
}
.el-input {
  width: 60%;
}
.gird-label {
  height: 40px;
  line-height: 40px;
}
.hw-layer-wrap {
  box-sizing: border-box;
  width: 450px;
  position: absolute;
  left: 55%;
  top: 15%;
  margin-left: -285px;
  border-radius: 3px;
  background-color: #fff;
  box-shadow: 1px 2px 4px 0 rgba(0, 0, 0, 0.12);
  padding: 45px 50px;
}
.hwLayer-close {
  position: absolute;
  right: 20px;
  top: 20px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  font-size: 20px;
  color: #ccc;
}
.hwLayer-close > img {
  width: 20px;
  height: 20px;
}
.line8 {
  width: 100%;
  height: 1px;
  background-color: grey;
  margin-top: 5px;
}
.btn-warning {
  float: right;
}
.hw-layer-wrap .hw-icon {
  color: #b4d8f3;
  font-size: 86px;
  text-align: center;
}
.hw-layer-wrap h4 {
  margin: 5px 0 30px;
  font-size: 24px;
  color: #383c3e;
}
.hw-layer-wrap p {
  margin: 30px 0;
  line-height: 22px;
  color: #595d60;
  text-align: left;
}
.hw-layer-title {
  height: 32px;
  line-height: 32px;
  font-size: 16px;
  position: absolute;
  left: 20px;
  top: 10px;
  font-weight: bold;
}
.fileName {
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-decoration: none;
  text-indent: 0;
  line-height: 20px;
}

@media (max-width: 768px) {
  .hw-layer-wrap {
    width: 350px;
    margin-left: -175px;
    margin-top: -200px;
    padding: 45px 50px;
    text-align: center;
  }
  #login_container {
    width: 50px;
  }
}
@media (max-width: 400px) {
  .hw-layer-wrap {
    width: 250px;
    margin-left: -125px;
    padding: 25px 30px;
  }
}
</style>



