<template>
  <div>
    <div style="height: 35px; width: 100%;"></div>
    <div class="footer">
      <div class="line"></div>
      <div class="footer-content">
        ©2018   云笈  	京ICP备18051964号-1 
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'HomeFooter'
}
</script>
<style lang="stylus" scoped>
@import '../common/css/reset.css';

.footer-content {
  height: 35px;
  line-height: 35px;
  text-align: center;
}

@media (min-width: 992px) {
  .footer {
    position: fixed;
    bottom: 0px;
    width: 60%;
    margin-left: 20%;
    // min-width: 750px;
    background: #ffffff;
    height: 35px;
    border-width: 60%;
    border-bottom: solid #f0f0f0;
  }

  .line {
    width: calc(100% + 40px);
    padding-left: -40px;
    height: 2px;
    background: #f5f6f7;
  }
}

@media (max-width: 992px) {
  .footer {
    position: fixed;
    bottom: 0px;
    width: 100%;
    background: #ffffff;
    height: 35px;
    border-width: 60%;
    border-bottom: solid #f0f0f0;
  }
  .line {
    width: 100%;
    height: 2px;
    background: #f5f6f7;
  }
}
</style>



