<template>
  <div class="content">
    <div class="content-header">
      <div class="hot-recommend">热点推荐</div>
      <div class="more-recommend">
        <router-link to='/moreAction'>
          <!-- 更多推荐 -->
        </router-link>
      </div>
      <div style="clear: both;"></div>
    </div>
    <div class="line"></div>
    <div class="content-article">
      <ul class="content-article-ul">
        <li v-for="(list, index) in articleList" class="content-article-li">
          <router-link :to="{name: 'detail', params: { id: (list.userId).toString() + list.articleId } }">
            <div class="article-headline"><h1>{{ list.title }}</h1></div>
            <div class="article-content">
              <p>
                {{ list.summary }}
              </p>
            </div>
            <div class="article-information">
              <div class="information-img">
                <img :src="list.headImageUrl" alt="" class="information-img-profile">
              </div>
              <div class="information-name">{{ list.username }}</div>
              <div class="information-date">{{ list.createtime | filerTime }}</div>
              <div style="clear: both;"></div>
            </div>
            <div class="line"></div>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="more-recommend-bottom">
      <el-button type="info" class="button-info" @click="getMoreArticleList">更多推荐</el-button>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'Header',
  data() {
    return {
      articleList: [],
      userIdarticleId: 'userIdarticleId',
      pagesize: 3,
      articleListTime: []
    }
  },
  methods: {
    getArticleList(articleId) {
      const _this = this
      if (articleId == 0) {
        axios
          .get('/yunji/article/webRecArticles?pagesize=' + _this.pagesize)
          .then(this.getArticleListInfo)
      } else {
        axios
          .get(
            '/yunji/article/webRecArticles?pagesize=' +
              _this.pagesize +
              '&articleId=' +
              articleId
          )
          .then(this.getArticleListInfo)
      }
    },
    getArticleListInfo(res) {
      const _this = this
      if (res.data.retBean.length == 0) {
        console.log('没有更多了！')
        _this.$message({
          message: '没有更多了！！',
          type: 'success'
        })
      }else{
        const articleId = res.data.retBean[res.data.retBean.length - 1].articleId
        _this.articleId = articleId
        this.articleList = this.articleList.concat(res.data.retBean)
      }
    },
    getMoreArticleList() {
      this.getArticleList(this.articleId)
    }
  },
  filters: {
    capitalizeTime: function(shijianchuo) {
      var time = new Date(shijianchuo)
      var y = time.getFullYear()
      var m = time.getMonth() + 1
      var d = time.getDate()
      var h = time.getHours()
      var mm = time.getMinutes()
      var s = time.getSeconds()
      return y + '-' + m + '-' + d
    },
    filerTime: function(value){
      if (!value) return ''
      value = value.toString()
      return value.substring(0,10)
    }
  },
  mounted() {
    this.getArticleList(0)
  }
}
</script>
<style lang="stylus" scoped>
@import '../common/css/reset.css';
@import '~styles/varibles.styl';

@media (min-width: 992px) {
  .content {
    width: 60%;
    margin-left: 20%;
    // min-width: 750px;
    background: #ffffff;
  }
}

@media (max-width: 992px) {
  .content {
    width: 100%;
    background: #ffffff;
  }
}

.content-header {
  height: 40px;
  line-height: 40px;
}

.hot-recommend {
  padding-left: 20px;
  float: left;
}

.more-recommend {
  padding-right: 20px;
  float: right;
}

.line {
  // width: calc(100% + 40px);
  // padding-left: -40px;
  height: 2px;
  background: #f5f6f7;
}

.content-article {
  width: 100%;
}

.content-article-li {
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.article-headline {
  color: #0f0f0f;
  font-size: 22px;
  margin-bottom: 15px;
}

.article-content {
  color: #666;
  font-size: 14px;
  margin-bottom: 10px;
}

.article-information {
  margin-bottom: 10px;
}

.information-img {
  float: left;
}

.information-img-profile {
  width: 30px;
  height: 30px;
}

.information-name {
  color: #666;
  float: left;
  margin-left: 15px;
  height: 30px;
  line-height: 30px;
}

.information-date {
  color: #666;
  margin-left: 15px;
  float: left;
  height: 30px;
  line-height: 30px;
}

li:hover {
  background: #f5f6f7;
}

.more-recommend-bottom {
  text-align: center;
  padding-bottom: 30px;
  padding-top: 20px;
}

.button-info {
  width: 120px;
  height: 45px;
  line-height: 45px;
}
</style>



